import React             from 'react';

import styles            from './RotatorType.css';

import { RotatorButton } from 'components/RotatorButton';
import { RotatorForm }   from 'components/RotatorForm';
import { RotatorPromo }  from 'components/RotatorPromo';
import { RotatorFull }   from 'components/RotatorFull';

export class RotatorType extends React.Component {
  static propTypes = {
    params: React.PropTypes.any
  };

  render() {
    const type = this.props.params.type;
    return (
      <div className = { styles.root }>
        { type === 'button' && <RotatorButton /> }
        { type === 'form'   && <RotatorForm /> }
        { type === 'promo'  && <RotatorPromo /> }
        { type === 'full'   && <RotatorFull /> }
      </div>
    );
  }
}



