import React    from 'react';
import { Link } from 'react-router';

import styles          from './Rotator.css';

export class Rotator extends React.Component {
  static propTypes = {
    children: React.PropTypes.any
  };

  render() {
    const showLinks = this.props.children === null;
    return (
      <div className = { styles.root }>
        { showLinks && <div className={ styles.nav }>
          <p className = { styles.text }>Examples of usage of the Rotator:</p>
          <ul className = { styles.list }>
            <li className = { styles.item }>
              <Link
                to        = "/rotator/button"
                className = { styles.itemLink }
              >
                Submit Button
              </Link>
            </li>
            <li className = { styles.item }>
              <Link
                to        = "/rotator/form"
                className = { styles.itemLink }
              >
                Auth Form
              </Link>
            </li>
            <li className = { styles.item }>
              <Link
                to        = "/rotator/promo"
                className = { styles.itemLink }
              >
                Promo Page
              </Link>
            </li>
            <li className = { styles.item }>
              <Link
                to        = "/rotator/full"
                className = { styles.itemLink }
              >
                Full Functionality
              </Link>
            </li>
          </ul>
        </div> }
        { !showLinks && this.props.children }
      </div>
    );
  }
}



